@import url("https://fonts.googleapis.com/css2?family=VT323&display=swap");

/*font-family: 'VT323', monospace;*/

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "VT323", monospace;
  color: #fff;
}

html,
body {
  background-image: linear-gradient(
    to bottom right,
    rgb(0, 0, 0),
    rgb(11, 15, 36)
  );
  height: 100%;
}

.PageContainer {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-content: space-between;
  align-items: center;
  height: 100%;
}

.Navbar {
  width: 80%;
  padding-top: 20px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  //border: 2px solid white;
}

.CajaVacia {
  width: 100px;
}

.NavLogos {
  display: flex;
  align-items: center;
  //border: 2px solid yellow;
}

.links {
  display: flex;
  justify-content: space-between;
  width: 260px;
  height: 100%;
  //border: 2px solid red;
}

.btnNAV {
  width: 110px;
  height: 50px;
  border: solid white 3px;
  color: white;
  background-color: rgb(0, 0, 0);
  border-radius: 17px;
  box-shadow: inset 0 0 0 0 rgb(242, 184, 49);
  transition: ease-out 0.3s;
  font-size: 2.5rem;
  outline: none;
  cursor: pointer;
}

.btnNAV:hover {
  background-color: rgb(79, 79, 79);
  //color: aqua;
}

.Hero {
  display: flex;
  flex-direction: column;
  justify-items: center;
  align-items: center;
  height: 100%;
  padding-top: 150px;
  //border: 2px solid red;
}

.Cara {
  /*border: 2px solid yellow;
  padding-top: 250px;*/
  animation: anicara 4s ease-in-out infinite;
}

@keyframes anicara {
  0% {
    transform: scale(1.5);
  }
  50% {
    transform: scale(2);
  }
  100% {
    transform: scale(1.5);
  }
}

.Soon {
  display: flex;
  padding-top: 40px;
  font-size: 36px;
  //border: 2px solid green;
  align-items: center;
  animation: anisoon 1s ease-in-out infinite;
}

@keyframes anisoon {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}

.btnCTA {
  width: 250px;
  height: 70px;
  border: none;
  color: white;
  background-color: blue;
  border-radius: 17px;
  box-shadow: inset 0 0 0 0 rgb(242, 184, 49);
  transition: ease-out 0.3s;
  font-size: 3.2rem;
  outline: none;
  cursor: pointer;
}

.btnCTA:hover {
  box-shadow: inset 300px 0 0 0 aqua;
  color: #000;
}

.Footer {
  padding-top: 250px;
  align-self: bottom;
  font-size: 15px;
}
